import { getApps } from './appManage';
import { rawWindow } from './raws';

// 
rawWindow.__ZMF_EVENT_LIST__ =
  rawWindow.__ZMF_EVENT_LIST__ || (rawWindow.__ZMF_EVENT_LIST__ = new Map());

function getCurrentAppName() {
  return window.__isZMF__ ? window.__APP_NAME__ : 'HOST';
}

// 
class EventCenter {
  // 
  eventList = rawWindow.__ZMF_EVENT_LIST__;
  /**
   * 
   * @param name 
   * @param f 
   */
  on(name, f) {
    //  app name
    const appNames = getApps()
      .map(el => el.appName)
      .concat(['HOST', 'GLOBAL']);
    if (!appNames.includes(name)) return;

    let eventInfo = this.eventList.get(name);
    // 
    if (!eventInfo) {
      eventInfo = {
        data: {},
        callbacks: new Set()
      };
      // 
      this.eventList.set(name, eventInfo);
    }

    // 
    eventInfo.callbacks.add(f);
  }

  // 
  off(name, f) {
    const eventInfo = this.eventList.get(name);
    // eventInfof,
    if (eventInfo) {
      if (typeof f === 'function') {
        eventInfo.callbacks.delete(f);
      } else {
        eventInfo.callbacks = new Set();
      }
    }
  }

  // 
  emit(data) {
    const name = getCurrentAppName();

    //   app name,  app .
    const appNames = getApps()
      .map(el => el.appName)
      .concat('HOST');
    if (!appNames.includes(name)) return;

    const eventInfo = this.eventList.get(name);
    // 
    if (eventInfo && eventInfo.data !== data) {
      eventInfo.data = data;
      // 
      for (const f of eventInfo.callbacks) {
        f(data);
      }
    }
  }
  // appName
  getData(name) {
    let eventInfo = this.eventList.get(name);
    if (eventInfo) {
      return eventInfo?.data;
    }
    return null;
  }
  // 
  createGlobalData(data) {
    this.eventList.set('GLOBAL', {
      data: data,
      callbacks: new Set()
    });
  }
  // 
  setGlobalData(data) {
    let eventInfo = this.eventList.get('GLOBAL');
    if (!eventInfo) {
      this.createGlobalData(data);
    }
    if (eventInfo && eventInfo.data !== data) {
      const mergeData = { ...eventInfo.data, ...data };
      eventInfo.data = mergeData;
      for (const f of eventInfo.callbacks) {
        f(mergeData);
      }
    }
  }
  // 
  getGlobalData() {
    return this.getData('GLOBAL');
  }
}

// 
export default new EventCenter();

